.fds-grid-container {
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 30vh 60vh;
    gap: 1vh;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.fds-grid-item{
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.fds-grid-item.ship-container {
    background-color: #222830;
}

.fds-grid-item.tank-container {
    background-color: #222830;
}

.fds-status-container{
    display: grid;
    height: 60vh;
    width: 100%;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr 1fr;
    column-gap: 1vw;
    row-gap: 0.5vh;
    /* overflow: column; */
    /* background-color: red; */
}

@media (orientation: landscape) {
    .fds-status-container{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr 1fr;
        column-gap: 0.8vw;
        row-gap: 1vh;
    }
}

.fds-status-item{
    padding: clamp(1px, 0.5vh, 20px);
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 3fr;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    color: #00cccc;
}

.fds-status-header{
    grid-row: span 1;
    font-weight: bold;
    font-size: clamp(1px, 2.5vh, 50px);
    /* padding-top: clamp(1px, 2vh, 20px); */
    /* border: 2px solid red; */
    display: grid;
    align-items: center;
}

.fds-grid-item.fds-grid-item.tank-container{
    overflow: auto;
}

.fds-status-body{
    /* border: 2px solid green; */
    grid-row: span 1;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    /* align-items: center; */
    /* gap: clamp(1px, 1vh, 20px); */
    column-gap: clamp(1px, 1vh, 20px);
    /* max-width: 100%;
    max-height: 5vh; */
}

.fds-status-body.bridge{
    /* border: 2px solid green; */
    grid-row: span 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    column-gap: clamp(1px, 1vh, 20px);
}

@media (orientation: landscape) {
    .fds-status-body{
        /* border: 2px solid green; */
        grid-row: span 1;
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        grid-template-rows: repeat(3, 1fr);
        /* align-items: center; */
        /* gap: clamp(1px, 1vh, 20px); */
        column-gap: clamp(1px, 1vh, 20px);
        /* max-width: 100%;
        max-height: 5vh; */
    }
    .fds-status-body.bridge{
        /* border: 2px solid green; */
        grid-row: span 1;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(4, 1fr);
        column-gap: clamp(1px, 1vh, 20px);
    }
}

.fds-indicators{
    /* border: 2px solid blue; */
    display: grid;
    grid-template-columns: 1fr 6fr;
    grid-template-rows: 1fr;
    /* gap: clamp(1px, 0.5vh, 100px); */
    /* font-size: clamp(1px, 1vh, 40px); */
    /* justify-items: center;
    align-items: center; */
    /* height: 3vh; */
    
    justify-items: start;
    align-items: center;
}

@media (max-height: 500px) {
  .fds-indicators {
    font-size: clamp(1px, 1.2vh, 40px);
  }
}

.fds-lamp{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    border-radius: 80px;
    height: 2vh;
    width: 2vh;
    font-size: clamp(1px, 1vh, 16px);
    font-weight: bold;
}

.fds-lamp-name{
    width: 100%;
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    /* place-items: start; */
    justify-items: start;
    align-items: center;
    height: 2vh;
    text-align: center;
    white-space: pre;         /* <-- This preserves spaces */
    word-break: keep-all;     /* <-- Prevents breaking between words */
    overflow-wrap: normal;    /* <-- Optional: reinforces keep-all */
}

.fds-lamp.open{
    background-color: #ff5c5c;
    color: #101417;
}

.fds-lamp.close{
    background-color: #66cc66;
    color: #101417;
}

.fds-status-item.main-deck{
    grid-column: span 3;
}

.fds-status-item.double-bottom{
    grid-column: span 5;
    
}

.fds-status-item.bridge-deck{
    grid-column: span 2;
}

.fds-lamp.sprinkler{
    border: 2px solid #888888;
    border-radius: 5px;
}