.me-overview-content-container.first{
  grid-column: 1;
  grid-row: 1;
}

.me-overview-content-container.second{
  grid-column: 2;
  grid-row: 1;
}

.me-overview-content-container.third {
  grid-column: 1 / -1;    /* span both columns */
  grid-row: 2;
}

.me-overview-content-container.hide{
  display: none;
}


.me-overview-content{
    width: 49vw;
    height: 33vh;
    /* border: 2px solid red ; */
    
    display: grid;
    grid-template-columns: 48vw;
    grid-template-rows: 34vh 10vh;
}

.me-overview-content-indicator{
  height: 33.5vh;
  /* background-color: red; */
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2.5vh 26vh 5vh;
}

.me-overview-content-indicator-container{
  /* background-color: blue; */
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 1vh;
}

