.me-overview-content-indicator-title{
  font-size: 2vh;
  font-weight: bold;
}

.avg-temp-box-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  /* background-color: #121212; */
}

.avg-temp-box {
  width: clamp(1px, 50%, 200px); /* responsive width */
  border: 2px solid #00cccc;
  border-radius: 4px;
  padding: 0.25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.1vh;       
  background: rgba(0,204,204,0.1);
}

@media (max-height: 500px) {
  .avg-temp-box {
    width: clamp(1px, 50%, 50px);
  }
}

.avg-temp-box .avg-temp-label {
  font-size: 1.5vh; /* slightly larger */
  font-weight: bold;
  color: #00cccc;
}

.avg-temp-box .avg-temp-value {
  font-size: 1.5vh;   /* a bit smaller */
  color: #ffffff;
}


.me-overview-content-status {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto auto;    /* row 1 = auto, row 2 = auto */
  gap: 1vh;
}

/* make the wrapper span all 5 columns, land on row 2 */
.second-row {
  grid-column: 1 / -1;
  grid-row: 2;
  
  /* now make *this* a 4‑col grid: */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1vh;
  
  /* center the whole block inside its area */
  justify-items: center;
}


/* 5) Lamps size with padding, not forced height */
.me-overview-content-status-lamp {
  border: 2px solid white;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0.2vh;
  font-size: 1vh;
  font-weight: bold;
  width: 100%;   /* fill its grid‐cell */
  height: 100%;
}

@media (max-height: 500px) {
  .me-overview-content-status-lamp {
    font-size: 0.8vh;
  }
}

.me-overview-content-status-lamp.onGreen {
  background-color: #00FF99;
  color: #121212;
}


.me-overview-content-status-lamp.onRed{
    background-color: #FF4C4C;
    color: #121212;
}