.alarm-record-container{
    /* background-color: blue; */
    height: 91vh;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 5fr;
    gap: 2px;
}

.alarm-record-header{
    /* background-color: red; */
    border: 2px solid #f0f0f0;
    border-radius: 2px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    padding: 0.5vh;
}

.alarm-record-header-content{
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-template-rows: 1fr;
  place-items: center;
  justify-items: start;
  
}
@media (orientation: landscape) {
  .alarm-record-header-content {
    display: grid;
    grid-template-columns: 1fr 5fr;
    justify-items: start;
  }
}

.alarm-record-header-content.activeAlarm{
    /* background-color: red; */
}

.alarm-record-header-content.lastAlarm{
    /* background-color: aqua; */
}

.alarm-record-header-content-counter{
  width: 100%;
  height: 100%;
  /* border: 2px solid #111; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0.5vh;
  
  /* border: 2px solid red; */
}

@media (orientation: landscape) {
  .alarm-record-header-content-counter {
    grid-template-columns: 4fr 1fr 1fr;
    width: 100%;
  }
}

.alarm-record-header-content-lastAlarm{
    border: 2px solid #f0f0f0;
    width: 100%;
    height: 60%;
    display: grid;
    place-items: center;
    justify-items: start;
    padding-left: 1vh;
    border-radius: 2px;
}

.alarm-record-header-content-counter-border{
  border: 2px solid #f0f0f0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  place-items: center;
  padding-bottom: 0.5vh;
  padding-top: 0.25vh;
  height: fit-content;
  font-weight: bold;
}

.alarm-record-table{
    background-color: green;
    border: 2px solid #f0f0f0;
    border-radius: 2px;
    overflow-y: auto;
    overflow-x: auto;
    /* padding: 1rem; */
    box-sizing: border-box;
}

.alarm-table {
  border-collapse: collapse;
  width: max-content;
  min-width: 100%;
}

.alarm-table th {
  background-color: #444;
  color: #f0f0f0;
  padding: 0.5rem 1rem;
  border: 1px solid #666;
  text-align: start;
}

.alarm-table td {
  padding: 0.5rem 1rem;
  border: 1px solid #444;
  color: #f0f0f0;
  white-space: nowrap;
}

.row-dark {
  background-color: #111;
}

.row-gray {
  background-color: #222;
}