.internalLight-grid-container {
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 45vh 45vh;
    gap: 1vh;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.internalLight-grid-item{
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.internalLight-grid-item.ship-container {
    background-color: #222830;
}

.internalLight-grid-item.tank-container {
    background-color: #222830;
}

.internalLight-status-container{
    display: grid;
    height: 45vh;
    width: 100%;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 1fr 1fr;
    column-gap: 1vw;
    row-gap: 0.5vh;
    /* background-color: red; */
}

@media (orientation: landscape) {
    .internalLight-status-container{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        grid-template-rows: 1fr 1fr;
        column-gap: 0.8vw;
        row-gap: 1vh;
    }
}

.internalLight-status-item{
    padding: clamp(1px, 0.5vh, 20px);
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 4fr;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    color: #00cccc;
}

.internalLight-status-header{
    grid-row: span 1;
    font-weight: bold;
    font-size: clamp(1px, 1.5vh, 50px);
    /* padding-top: clamp(1px, 2vh, 20px); */
    /* border: 2px solid red; */
    display: grid;
    align-items: center;
}

.internalLight-grid-item.internalLight-grid-item.tank-container{
    overflow: auto;
}

.internalLight-status-body{
    /* border: 2px solid blue; */
    /* grid-row: span 1; */
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    /* column-gap: clamp(1px, 1vh, 20px); */
}

.internalLight-status-body.bridge{
    /* border: 2px solid green; */
    grid-row: span 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
    /* column-gap: clamp(1px, 1vh, 20px); */
}

@media (orientation: landscape) {
    .internalLight-status-body{
        /* border: 2px solid green; */
        grid-row: span 1;
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        grid-template-rows: repeat(3, 1fr);
        /* align-items: center; */
        /* gap: clamp(1px, 1vh, 20px); */
        column-gap: clamp(1px, 1vh, 20px);
        /* max-width: 100%;
        max-height: 5vh; */
    }
    .internalLight-status-body.bridge{
        /* border: 2px solid green; */
        grid-row: span 1;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(1, 1fr);
        /* column-gap: clamp(1px, 1vh, 20px); */
    }
}

.internalLight-indicators{
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 1fr 6fr;
    /* grid-template-rows: repeat(4, 1fr); */
    gap: clamp(1px, 1vh, 100px);
    font-size: clamp(1px, 1.2vh, 16px);
}

@media (max-height: 500px) {
  .fds-indicators {
    font-size: clamp(1px, 1.2vh, 40px);
  }
}

.internalLight-lamp{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    height: 2vh;
    width: 3vh;
    border-radius: 2px;
    font-size: clamp(1px, 1vh, 16px);
    font-weight: bold;
}

.internalLight-lamp-name{
    width: 100%;
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    /* place-items: start; */
    justify-items: start;
    align-items: center;
    height: 2vh;
    text-align: center;
    white-space: pre;         /* <-- This preserves spaces */
    word-break: keep-all;     /* <-- Prevents breaking between words */
    overflow-wrap: normal; 
    font-size: clamp(0.5vh, 1.5vw, 1.5vh);
}

.internalLight-lamp.off{
    border: 2px solid #f0f0f0;
    background-color: #333;
    color: #f0f0f0;
}

.internalLight-lamp.on{
    border: 2px solid #f0f0f0;
    background-color: #66cc66;
    color: #101417;
}

.internalLight-display-lamp{
    border: 2px solid #f0f0f0;
    color: #101417;
}

.internalLight-display-lamp.off{
    background-color: #333;
}

.internalLight-display-lamp.on{
    background-color: #66cc66;
}

.internalLight-status-item.main-deck{
    grid-column: span 4;
}

.internalLight-status-item.double-bottom{
    grid-column: span 5;
    
}

.internalLight-status-item.bridge-deck{
    grid-column: span 1;
}