.tank-grid-container {
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 45vh 45vh;
    gap: 1vh;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.tank-grid-item{
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.tank-grid-item.ship-container {
    background-color: #222830;
}

.tank-grid-item.tank-container {
    background-color: #222830;
}

.ship-map-for-tanks{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: grid;
    /* grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr); */
}

.ship-svg{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

