.wtd-grid-container {
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 60vh 30vh;
    gap: 1vh;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.wtd-grid-item{
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.wtd-grid-item.ship-container {
    background-color: #222830;
}

.wtd-grid-item.tank-container {
    background-color: #222830;
}

.wtd-status-container{
    display: grid;
    height: 30vh;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    column-gap: 1vw;
    row-gap: 0.5vh;
    /* background-color: red; */
}

@media (orientation: landscape) {
    .wtd-status-container{
        display: grid;
        height: 30vh;
        width: 100%;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        column-gap: 0.8vw;
        row-gap: 1vh;
    }
}

.wtd-status-item{
    padding: clamp(1px, 1vh, 20px);
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    color: #00cccc;
}

.wtd-status-header{
    grid-row: span 1;
    font-weight: bold;
    font-size: clamp(1px, 2.5vh, 50px);
    padding-top: clamp(1px, 2vh, 20px);
    /* border: 2px solid red; */
}

.wtd-status-body{
    /* border: 2px solid blue; */
    grid-row: span 1;
    display: grid;
    grid-template-rows: 1fr;
    align-items: center;
    gap: clamp(1px, 2vh, 20px);
}

.wtd-indicators{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    gap: clamp(1px, 1vh, 100px);
    font-size: clamp(1px, 1.2vh, 16px);
}

.wtd-lamp{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    height: 4vh;
    border-radius: 2px;
    font-size: clamp(1px, 1vh, 50px);
    font-weight: bold;
}

@media (orientation: landscape) {
    .wtd-lamp{
        font-size: clamp(1px, 1.5vh, 50px);
    }
}



.wtd-lamp-name{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
    justify-items: start;
    /* border: 2px solid #f0f0f0;
    background-color: #101417; */
    font-size: clamp(1px, 1.2vh, 50px);
    height: 4vh;
}

@media (orientation: landscape) {
    .wtd-lamp-name{
        font-size: clamp(1px, 2vh, 50px);
    }
}

.wtd-lamp.open{
    border: 2px solid #f0f0f0;
    background-color: #ff5c5c;
    color: #101417;
}

.wtd-lamp.close{
    border: 2px solid #f0f0f0;
    background-color: #66cc66;
    color: #101417;
}

.wtd-status-item.main-deck{
    grid-column: span 3;
}

.wtd-status-item.double-bottom{
    grid-column: span 2;
    
}

.wtd-status-item.bridge-deck{
    grid-column: 3;
}