.bordered-text-box-container {
  display: grid;
  grid-template-rows: 1fr 1.5fr;
  border: 3px solid #00cccc;
  border-radius: 5px;
  padding: 0.2vh;
  width: 100%;
}

.bordered-text-box-top-text {
  width: 100%;
  font-size: clamp(1px, 2vh, 50px);
  text-align: center;
  /* background-color: #00cccc; */
  display: flex;
  align-items: flex-end;   /* push text to bottom of cell */
  justify-content: center;
  font-weight: bold;
}

.bordered-text-box-bottom-text {
  width: 100%;
  font-size: clamp(1px, 2.5vh, 50px);
  text-align: center;
  /* background-color: red; */
  display: flex;
  align-items: flex-start; /* push text to top of cell */
  justify-content: center;
}

