.battrey-text-box-container {
  display: grid;
  grid-template-rows: 1fr 1.5fr;
  border: 3px solid #00cccc;
  border-radius: 5px;
  padding: 0.2vh;
  width: 100%;
}

.battrey-text-box-top-text {
  width: 100%;
  font-size: clamp(1px, 2vh, 50px);
  text-align: center;
  /* background-color: #00cccc; */
  display: flex;
  align-items: flex-end;   /* push text to bottom of cell */
  justify-content: center;
  font-weight: bold;
}

.battrey-text-box-middle-text {
  width: 100%;
  font-size: clamp(1px, 2.5vh, 50px);
  text-align: center;
  background-color: red;
  display: flex;
  align-items: flex-start; /* push text to top of cell */
  justify-content: center;
}

.battrey-text-box-bottom-text-container{
  /* width: 100%; */
  /* height: 100%; */
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 1fr;
  /* background-color: blue; */
  /* padding: 0.5vh; */
}

.battrey-text-box-bottom-text {
  width: 100%;
  height: 100%;
  font-size: clamp(0.5vh, 1.5vh, 2vh);
  text-align: center;
  /* background-color: blue; */
  display: flex;
  align-items: center; /* push text to top of cell */
  justify-content: center;
}

.battrey-text-box-bottom-text.battrey{
  width: auto;
  height: auto;
  max-height: 100%;
  background-color: blue;
  display: block;
}

.battrey-text-box-bottom-text.unit{
  width: 100%;
  font-size: clamp(0.5vh, 1.5vh, 2vh);
  text-align: center;
  /* background-color: blue; */
  display: flex;
  align-items: center; /* push text to top of cell */
  justify-content: center;
}

