.alarm-list-container {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
  padding: 1rem;
  height: 91vh;
  width: 100%;
  box-sizing: border-box;
  /* gap: 1rem; — removed for fallback */
}

.alarm-box {
  border: 2px solid #cc0000;
  /* padding: 1vh; */
  min-width: 10px;
  min-height: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ff5c5c;
  color: #333;
  font-weight: bold;
  font-size: 3vh;
  border-radius: 0.5rem;
  flex-shrink: 0;
  transition: background-color 0.3s;
  cursor: pointer;
  /* Fallback spacing */
  margin: clamp(1px, 1vh, 5px);
}

/* Portrait: vertical scroll, 4 rows, wrap to new column */
@media (orientation: portrait) {
  .alarm-list-container {
    flex-direction: column;
  }

  .alarm-box {
    flex: 0 0 calc((91vh) / 7); /* 4 rows */
    width: 80%;
    /* height: 40%; */
  }
}

/* Landscape: horizontal scroll, 4 columns, wrap to new row */
@media (orientation: landscape) {
  .alarm-list-container {
    flex-direction: column;
  }

  .alarm-box {
    /* flex: 0 0 calc((91vh) / 4); */
    width: 30%;
    height: calc(91vh / 6);
  }
}



.alarm-box.acknowledged {
  background-color: #d4eed4;
  border-color: #88cc88;
}
