.navLight-grid-container {
    display: grid;
    grid-template-columns: 1fr; 
    grid-template-rows: 75vh 15vh;
    gap: 1vh;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.navLight-grid-item{
    display: grid;
    place-items: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
}

.navLight-grid-item.ship-container {
    background-color: #222830;
}

.navLight-grid-item.tank-container {
    background-color: #222830;
}

.navLight-status-container{
    display: grid;
    height: 15vh;
    width: 100%;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    column-gap: 1vw;
    row-gap: 0.5vh;
    /* background-color: red; */
}

