.navLight-status-item{
    padding: clamp(1px, 1vh, 20px);
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 4fr;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    color: #00cccc;
}

.navLight-status-header{
    grid-row: span 1;
    font-weight: bold;
    font-size: clamp(1px, 1.5vh, 50px);
    /* padding-top: clamp(1px, 2vh, 20px); */
    /* border: 2px solid red; */
    display: grid;
    align-items: start;
    /* border: 2px solid red; */
}

.navLight-grid-item.navLight-grid-item.tank-container{
    overflow: auto;
}

.navLight-status-body{
    /* border: 2px solid blue; */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.navLight-indicators{
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 1fr 6fr;
    /* grid-template-rows: repeat(4, 1fr); */
    gap: clamp(1px, 1vh, 100px);
    font-size: clamp(1px, 1.2vh, 16px);
}

.navLight-lamp{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
    border: 2px solid #f0f0f0;
    background-color: #101417;
    height: 2vh;
    width: 3vh;
    border-radius: 2px;
    font-size: clamp(1px, 1vh, 16px);
    font-weight: bold;
    cursor: pointer;
}

.navLight-lamp-name{
    width: 100%;
    /* border: 2px solid red; */
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    /* place-items: start; */
    justify-items: start;
    align-items: center;
    height: 2vh;
    text-align: center;
    white-space: pre;         /* <-- This preserves spaces */
    word-break: keep-all;     /* <-- Prevents breaking between words */
    overflow-wrap: normal; 
    font-size: clamp(0.5vh, 1.5vw, 1.5vh);
}

.navLight-lamp.on{
    border: 2px solid #f0f0f0;
    background-color: #66cc66;
    color: #101417;
}

.navLight-lamp.off{
    border: 2px solid #f0f0f0;
    background-color: #333;
    color: #f0f0f0;
}

.navLight-status-item.main-deck{
    grid-column: span 3;
}

.navLight-status-item.double-bottom{
    grid-column: span 2;
    
}

.navLight-status-item.bridge-deck{
    grid-column: span 1;
}