.ae-grid-container{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80vh 10vh;
    width: 100%;
    height: 100%;
    gap: 1vh;
}


.ae-indicators{
    border: 0.5vh solid #f0f0f0;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 1fr;
    height: 80vh;
    /* grid-template-columns: 65.33vw 32.66vw; */
}

/* .ae-indicators-grid-item {
  text-align: center;
  box-sizing: border-box;
} */

.ae-indicators-content{
    display: grid;
    box-sizing: border-box;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

.ae-indicators-content.left{
    display: grid;
    /* background-color: #00FF99; */
    grid-template-rows: 2fr 1fr 1fr;
    grid-template-columns: 1fr;
    height: 100%;
    padding: 1vh;
}

@media (orientation: landscape) {
    .ae-indicators-content.left{
        grid-template-rows: 30vh 23.5vh 23.5vh;
    }
}

.ae-indicators-content-left-items{
    /* border: 2px solid red; */
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    place-items: center;
}

.bordered-text-box-icon {
  width: 100%;
  height: auto;
  object-fit: contain;
  justify-self: start;
  align-self: center;
}

@media (orientation: landscape) {
    .bordered-text-box-icon{
        width: 50%;
    }
}

.ae-indicators-content-left-items.indicators-middle{
    gap: 1vh;
    grid-template-columns: 1fr 1fr 0.5fr;
}

.ae-indicators-content-left-items.indicators-bottom{
    gap: 1vh;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    height: 19vh;
}

@media (orientation: landscape) {
    .ae-indicators-content-left-items.indicators-bottom{
        height: 23.5vh;
    }
}

.ae-indicators-content.right{
    display: grid;
    /* background-color: #00cccc; */
    grid-template-rows: 26.333vh 26.333vh 26.333vh;
    width: 100%;
    height: 79vh;
    box-sizing: border-box;
}

/* @media (orientation: landscape) {
  .ae-grid-container {
    grid-template-rows: 80vh 10vh;
  }
  .ae-indicators-content.right{
    height: 80vh;
  }
} */

.ae-indicators-content.right-center{
    /* border: 2px solid red; */
    grid-template-columns: 1fr 1fr;
}

.ae-status{
    background-color: #121212;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    height: 10vh;
    width: 100%;
    gap: 1%;
}

.ae-status-lamp{
    display: grid;
    place-items: center;
    border: 2px solid #00cccc;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 2vh;
    color: #00cccc;
    border-radius: 2px;
    font-weight: bold;
}

.ae-status-lamp.green{
    background-color: #00FF99;
    color: #121212;
}

.ae-status-lamp.red{
    background-color: #FF4C4C;
    color: #121212;
}