.me-display-grid-container {
  display: grid;
  grid-template-columns: 1fr; 
  grid-template-rows: 45vh 45vh;
  gap: 1vh;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  /* background-color: blue; */
}

@media (orientation: landscape) {
  .me-display-grid-container {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr; 
  }
}

.me-display-grid-container-right-cylinder {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  padding-left: 3vh;
  padding-right: 3vh;
  width: 100%;
  height: 100%;
  border: #f0f0f0 0.2vh solid;
  box-sizing: border-box;
}

.me-display-grid-container-right-portrait {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.me-display-grid-container-right-portrait.subcontainer {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-template-rows: calc((45vh - 8vh)/2) calc((45vh - 8vh)/2);
  padding: 0%;
  width: 100%;
  height: calc(45vh - 8vh);
  box-sizing: border-box;
}

.me-display-grid-container-right-portrait.leftLean {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 1fr 2fr 2fr;
  grid-template-rows: auto;
}

.me-display-grid-container-right-portrait.rightLean {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 2fr 2fr 1fr;
  grid-template-rows: auto auto;
}

@media (orientation: landscape) {
  .me-display-grid-container-right-portrait {
    display: none; 
  }
}

.me-display-grid-container-right {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: calc((100vh - 8vh - 2vh - 8vh) / 3) calc((100vh - 8vh - 2vh - 8vh) / 3) calc((100vh - 8vh - 2vh - 8vh) / 3) 8vh;
  padding: 0.5vh;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.me-display-grid-container-right.leftLean {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  grid-template-rows: calc((100vh - 8vh - 2vh - 8vh) / 3);
}

.me-display-grid-container-right.rightLean {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  grid-template-rows: calc((100vh - 8vh - 2vh - 8vh) / 3);
}

@media (orientation: portrait) {
  .me-display-grid-container-right {
    display: none; 
  }
}

.me-display-grid-container-nav-buttons {
  display: grid;
  grid-template-columns: 18fr 8vh 8vh;
  grid-template-rows: 1fr;
  gap: 1%;
  padding: 0.5%;
  width: 100%;
  height: 8vh;
  box-sizing: border-box;
}

.me-display-grid-item {
  text-align: center;
  box-sizing: border-box;
}

.me-display-grid-item.navbuttons {
  grid-column: 1;
}

@media (orientation: landscape) {
  .me-display-grid-item.navbuttons {
    grid-column: span 2;
  }
}

.me-display-item5 {
  text-align: center;
  border: 1px solid #282c34;
  box-sizing: border-box;
  grid-column: 1 / span 2;
  grid-row: 3;
}

.me-display-nav-btn {
  border-radius: 50%;
  border: 2px solid #00cccc;
  background-color: #1a1a1a;
  color: #00cccc;
  font-size: 3vh;
  font-weight: bold;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  outline: none;
}

.me-display-nav-btn:hover {
  background-color: #222222;
}

.me-display-nav-btn:active {
  background-color: #2a2a2a;
}
