*, *::before, *::after {
  box-sizing: border-box;
  /* overflow: hidden; */
}
.App{
  height: 100vh; 
  width: 100vw;
  background-color: #121212;
}

.nav-header {
  display: grid;
  /* padding: 0.5vh; */
  gap: 0.5vh;
  grid-template-columns: auto 1.3fr 1.4fr 0.8fr 1fr; /* Logo | Spacer | Links */
  align-items: center;
  height: 8vh; /* ← set height here */
  width: 100%;
  background-color: #121212;
  color: #00cccc;
  font-weight: bold;
}

.icon-button {
  width: calc(7.8vh);
  height: calc(7.8vh);
  background: transparent;
  border: 2px solid #00cccc;
  border-radius: 6px;
  /* padding: 0.5vh; */
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    inset 2px 2px 5px rgba(0, 255, 255, 0.3),
    2px 2px 5px rgba(0, 0, 0, 0.7);
  transition: border-color 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
}

.icon-button img {
  position: block;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.icon-button:hover {
  border-color: #00ffffcc;  /* brighter aqua */
  box-shadow:
    inset 2px 2px 8px rgba(0, 255, 255, 0.6),
    2px 2px 10px rgba(0, 255, 255, 0.4);
  background-color: rgba(0, 255, 255, 0.1);
}

.icon-button:active {
  border-color: #00cccc;  /* darker aqua on click */
  box-shadow:
    inset 1px 1px 4px rgba(0, 204, 204, 0.7),
    1px 1px 5px rgba(0, 204, 204, 0.5);
  background-color: rgba(0, 204, 204, 0.2);
}

.icon-button:hover img {
  filter: drop-shadow(0 0 3px #00ffff);
}

.icon-button:active img {
  filter: drop-shadow(0 0 5px #00cccc);
}

