.header-infoBox{
  display: flex;
  padding: 0.8vh;
  border: 2px solid #00cccc;
  border-radius: 2%;
  align-items: center;
  justify-content: start;
  width: 100%;
  height: calc(8vh - 2px); 
  font-size: 2vh;
}

.header-infoBox-dateTime{
  font-size: 1.5vh;
  display: flex;
  border: 2px solid #00cccc;
  border-radius: 2%;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; /* ← set height here */
}

.header-dateTime{
  display: grid;
  gap: 0.5vh;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  /* background-color: blue; */
  width: 100%;
  height: calc(8vh - 2px);
}

.content-container {
  padding: 0.5vh;
  width: 100%;
  height: calc(100vh - 8vh); /* ← set height here */
  background-color: #121212;
  color: #f0f0f0;
}

