/* Common sidebar styles */
.sidebar-overlay {
  position: fixed;
  top: 0;
  height: fit-content;
  padding: 2vh;
  color: white;
  background: #111;
  box-shadow: 20px 0 25px rgba(0, 255, 255, 0.2);
  z-index: 1000;
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  border-right: 3px solid #00cccc;
  border-bottom: 3px solid #00cccc;
}

.sidebar-overlay.primary {
  left: 0;
  width: 25vw;
  height: fit-content;
  min-height: 45vh;
}

.sidebar-overlay.secondary {
  left: 25vw;
  width: 40vw;
  height: fit-content;
  min-height: 38vh;
  background: #111;
  border-left: 1px solid #00cccc;
  z-index: 1001;
}

@media (orientation: landscape) {
  .sidebar-overlay.primary {
    width: 15vw;
    height: fit-content;
    min-height: 45vh;
  }

  .sidebar-overlay.secondary {
    left: 15vw;
    width: 18vw;
    height: fit-content;
    min-height: 38vh;
  }
}

.sidebar-overlay h2, .sidebar-overlay h3 {
  font-size: 3vh;
  color: #00cccc;
  border-bottom: 1px solid #00cccc;
  margin-bottom: 2vh;
}

/* Buttons and links */
.sidebar-overlay nav a,
.sidebar-overlay nav button {
  color: white;
  background: none;
  border: none;
  text-align: left;
  font-size: 2vh;
  padding: 1.5vh 0;
  cursor: pointer;
  border-left: 3px solid transparent;
  transition: all 0.3s ease;
}

.sidebar-overlay nav a:hover,
.sidebar-overlay nav button:hover {
  color: #00cccc;
  background: rgba(0, 255, 255, 0.1);
  border-left: 3px solid #00cccc;
  padding-left: 8px;
}

/* Backdrop */
.sidebar-overlay-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 900;
}
